.App {
  padding: 0;
  margin-left: auto;
  margin-right: auto;
  max-height: 100vh;
  min-height: 100vh;
  max-width: 480px;
  min-width: 335px;
  background-color: #fff;
  background-image: url("../public/wa_bg.png");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}
.header {
  position: fixed;
  top: 0;
  width: 100%;
  max-width: 480px;
}
.input {
  background-color: #fff;
  border-color: #f2f2f2;
  outline: #f2f2f2;
  resize: none;
  border-radius: 26px;
  height: 30px;
  min-height: 30px;
  max-height: 70px;
  width: calc(100% - 135px);
  padding: 10px 18px 10px 45px;
  margin: 2px 5px;
  font-size: 18px;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  background-image: url("../public/icons/emoji_emotions_black.svg");
  background-position-x: 5px;
  background-position-y: 5px;
  background-repeat: no-repeat;
  background-size: contain;
}
.input::focus {
  border-color: #f2f2f2;
  outline: #f2f2f2;
}
.divider {
  height: 10px;
  background-color: transparent;
}
.footer {
  position: fixed;
  bottom: 8px;
  width: 100%;
  max-width: 480px;
}
.enter-msg {
  display: flex;
}
.send-btn {
  height: 52px;
  width: 52px;
  margin: 2px 0;
  border-radius: 50%;
  border: 1px solid #a7a7a7;
  background-color: #00a884;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}
.option-btn {
  width: 70%;
  text-align: center;
  background-color: #00a884;
  font-size: 18px;
  color: #fff;
  border-radius: 20px;
  border: 0.5px solid #f2f2f2;
  margin: 0 auto 8px;
  padding: 10px 15px;
  cursor: pointer;
}
small {
  font-size: 8px;
  text-align: center;
  opacity: 0.7;
  letter-spacing: 1.2px;
  text-shadow: 0 0 black;
  /* color: #00a884; */
  position: fixed;
  bottom: 2px;
  width: 100%;
  max-width: 480px;
}
.chats {
  scroll-behavior: smooth;
  position: fixed;
  width: 100%;
  max-width: 480px;
  padding: 10px;
  overflow-y: auto;
  margin-top: 62px;
}
.chat-body {
  width: 95%;
}
